<template>
    <section class="operation">
        <el-table :data="lists"
                  stripe
                  border
                  class="list">
            <el-table-column
                    prop="gate_id"
                    label="闸口">
            </el-table-column>
            <el-table-column
                    prop="chnl_no"
                    label="通道">
            </el-table-column>
            <el-table-column
                    prop="_seq_no"
                    label="序列号">
            </el-table-column>
            <el-table-column
                    prop="PLC"
                    label="类型">
            </el-table-column>
            <el-table-column
                    prop="_i_e_type"
                    label="进出标识">
            </el-table-column>
            <el-table-column
                    prop="_value"
                    label="value">
            </el-table-column>
            <el-table-column
                    prop="user"
                    label="user">
            </el-table-column>
        </el-table>
        <el-pagination class="pagination"
                       @current-change="handleCurrentChange"
                       :page-count="pageTotal"
                       layout="total, prev, pager, next, jumper"
        >
        </el-pagination>
    </section>
</template>

<script>
import api from "@/api/index"
export default {
    data() {
        return {
            token:localStorage.getItem("token"),
            lists:[],
            pageTotal:0
        }
    },
    methods: {
        getList(page){
            api.getOperationRecord({
                token:this.token,
                page_idx:page,
                page_cap:6,
            }).then((res) =>{
                console.log(res);
                this.lists = res.page_data
                this.pageTotal = res.page_total_cnts
            })
        },
        handleCurrentChange(page){
            this.getList(page)
        }
    },
    created() {
        this.getList(1)
    }

}
</script>

<style scoped lang="scss">
.operation{
    width: 100%;
    .list{
        width: 100%;
        margin-top: 10px;
    }
    .pagination{
        margin-top: 15px;
    }
}
</style>



